import React from "react";
import $ from 'jquery';
import { Table } from 'react-bootstrap';
import { Button } from "react-bootstrap";
import '../App.css';
import { api } from "../api";
import axios from "axios";

export default class JobManager extends React.Component{
    jobs = [];
    studentName = "";
    subject = "";
    grade = 0;
    area = "";
    description = "";

   constructor(props){
    super(props)
    this.state = {jobs: props.jobs};  

    this.NewJobForm = this.NewJobForm.bind(this);
    this.JobDeletionForm = this.JobDeletionForm.bind(this);
    this.GetJobList = this.GetJobList.bind(this);
   }

   //get delete and post updates to reflect in state - WORKING I THINK
   render(){
    return(
        <div>
            <this.JobDeletionForm/><br/>
            <this.NewJobForm />
        </div>
    )
   }

   NewJobForm(){
    return(
        <div className="job-box">
            <input onChange={this.GetStudName} className="new-job-input" id="inJobStudentName" placeholder="Student name"></input>
            <input onChange={this.GetSubject} className="new-job-input" id="inJobSubject" placeholder="Subject"></input>
            <input onChange={this.GetGrade} className="new-job-input" id="inJobGrade" placeholder="Grade"></input>
            <input onChange={this.GetArea} className="new-job-input" id="inJobArea" placeholder="Area"></input>
            <input onChange={this.GetDesc} className="new-job-input" id="inJobDesc" placeholder="Description"></input>
            <Button id="btnPostJob" variant="primary" onClick={async() => await this.PostJob()}>Post</Button>
        </div>
    )
   }

   async GetJobList(){
        await api.get('/jobs' , {})
        .then((res) => {
            console.log("Job Manager retrieved: ", res.data.jobs);
            this.setState( {jobs: Array.from(res.data.jobs)} );
        })
   }

   async PostJob(){
        await axios.post('/job/add', {}, {
            headers: {
                'student_name': this.studentName,
                'subject' : this.subject,
                'grade': this.grade,
                'area': this.area,
                'description': this.description
            }
        })
        .then(async() => {
            await this.GetJobList();
        })
   }

   GetStudName = (event) => {
    this.studentName = event.target.value;
    console.log("Student name: "+this.studentName);
   }

   GetSubject = (event) => {
    this.subject = event.target.value;
    console.log("Subject: "+this.studentName);
   }

   GetGrade = (event) => {
    this.grade = event.target.value;
    console.log("Grade: "+this.grade);
   }

   GetArea = (event) => {
    this.area = event.target.value;
    console.log("Area: "+this.area);
   }

   GetDesc = (event) => {
    this.description = event.target.value;
    console.log("Description: "+this.description);
   }

   async DeleteJob(id){
    //update jobs in JobManager state
    //make call to job editing endpoint with job data as headers
    //update in database
     
     axios.post('/job/delete/'+id, {})
     .then((res) => {
        console.log("Deleted document: \n", res.data);
     })
     .then(async() => {
        await this.GetJobList();
     })
    

     await this.GetJobList()
     .then((res) => {
        console.log(res);
     })
   }

   JobDeletionForm(){  
    $(document).ready(function(){
        $("tr:odd").css({
            "background-color": "lightgrey"
        });
        $("tr:even").css({
            "background-color": "white"
        });
        $("th").css({
            "background-color": "black",
            "color": "white"
        })
    })
    
    return(
        <Table hover>
            <thead>
                <tr>
                    <th>Student</th>
                    <th>Subject</th>
                    <th>Grade</th>
                    <th>Area</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
            {(this.state.jobs).map(job => (
                <tr key={job.student_name+Math.random()*100}>
                    <td>{job.student_name}</td>
                    <td>{job.subject}</td>
                    <td>{job.grade}</td>
                    <td>{job.area}</td>
                    <td><Button variant="primary" onClick={async() => await this.DeleteJob(job._id)}>Delete</Button></td>
                </tr>
            ))}
            </tbody>            
        </Table>
    )
   }
}