import React from "react";
import $ from 'jquery';
import { Table } from 'react-bootstrap';
import { Button } from "react-bootstrap"
import axios from "axios";
import secrets from "../secrets";
import CryptoJS from "crypto-js";

export default class UserInfoBoard extends React.Component {
    constructor(props){
        super(props);

        this.usersCounter = -1;
        /*
        this.name = props.user.name;
        this.id = props.user.id;
        this.phoneNum = props.user.phoneNum;
        this.email = props.user.email;
        this.accountNo = props.user.accountNo;
        this.university = props.user.university;
        this.courseName = props.user.courseName;
        this.dob = props.user.dob;
        this.approved = props.user.approved;
        this.role = props.user.role;
        this.area = props.user.area;
        */

        this.state = {users: props.users};

        this.UserInfoForm = this.UserInfoForm.bind(this);
        this.DeleteUser = this.DeleteUser.bind(this);
        this.GetUserList = this.GetUserList.bind(this);
        this.FormatDate = this.FormatDate.bind(this);
        this.IncUserCounter = this.IncUserCounter.bind(this);
    }

    render(){
        return(
            <div>
                <this.UserInfoForm/><br/>
            </div>                
        )
    }

    FormatDate(dob){
        let shortenedDate = dob.substr(0, 10);
        return shortenedDate;
    }

    ResetUserCounter(){
        this.usersCounter = 0;
    }

    IncUserCounter(){
        ++this.usersCounter;
        console.log("USER COUNTER: "+this.usersCounter);
    }

    UserInfoForm(){
        $(document).ready(function(){
            $("tr:odd").css({
                "background-color": "lightgrey"
            });
            $("tr:even").css({
                "background-color": "white"
            });
            $("th").css({
                "background-color": "black",
                "color": "white"
            })
        })

        //let decryptedAccNo = CryptoJS.AES.decrypt(user.account_no, secrets.SECRET_KEY);

        //RESUME HERE: why second and third tables not being padded?
        //              why are dobs and acc nums not being iterated over?
        let decryptedAccNums = [];
        let dobs = [];
        let currAccNum = "";

        let arrUsers = Array.from(this.state.users);
        arrUsers.forEach((user) => {
            currAccNum = CryptoJS.AES.decrypt(user.account_no, secrets.SECRET_KEY).toString(CryptoJS.enc.Utf8);
            decryptedAccNums.push(currAccNum);
            dobs.push(this.FormatDate(user.date_of_birth));
            console.log("Added "+user.name+" with acc num "+currAccNum);
        })

        /*
        (this.state.users).map(user => {
            //decryptedAccNums.push(CryptoJS.AES.decrypt(user.account_no, secrets.SECRET_KEY).toString(CryptoJS.enc.Utf8));
            //decryptedAccNums.push(user.account_no);
            //dobs.push(this.FormatDate(user.date_of_birth));
            //++this.usersCounter;
        })
        */

        //let usersCounter = 0;
        //decryptedAccNo = CryptoJS.AES.decrypt(user.account_no, secrets.SECRET_KEY).toString(CryptoJS.enc.Utf8)
        //decryptedAccNo = decryptedAccNo.toString(CryptoJS.enc.Utf8);

        return(
            <div>
                <h1>Personal information</h1><br/>
                <Table id="tblPersonalInfo" hover>
                    <thead>
                        <tr>
                            <th></th>
                            <th>Name</th>
                            <th>ID</th>
                            <th>Date of birth</th>
                            <th>Approved</th>
                            <th>Role</th>
                            <th>Area</th>     
                            <th></th>              
                        </tr>
                    </thead>
                    <tbody>
                    {(this.state.users).map(user => (
                        <tr key={user.ID+Math.random()*100}>
                            {/*this.IncUserCounter*/}
                            {<p className="textHidden">{++this.usersCounter}</p>}
                            <td>{user.name}</td>
                            <td>{user.ID}</td>
                            <td>{dobs[this.usersCounter]}</td>
                            <td>{user.approved ? "Yes" : "No"}</td>
                            <td>{user.role}</td>
                            <td>{user.area}</td>
                            <td><Button variant="primary" onClick={async() => await this.DeleteUser(user._id)}>Delete</Button></td>
                        </tr>
                    ))}
                    </tbody>            
                </Table>

                {<h1 className="textHidden">0</h1>}
                <h1>Contact information</h1><br/>
                <Table id="tblContactInfo" hover>
                    <thead>
                        <tr>
                            <th className="paddedHeading">Name</th>
                            <th className="paddedHeading">Phone number</th>
                            <th className="paddedHeading">Email</th>
                            
                        </tr>
                    </thead>
                    <tbody>
                    {(this.state.users).map(user => (
                        <tr key={user.ID+Math.random()*100}>
                            <td className="paddedRow">{user.name}</td>
                            <td className="paddedRow">{user.phone_no}</td>
                            <td className="paddedRow">{user.email}</td>
                        </tr>
                    ))}
                    </tbody>            
                </Table>

                {<h1 className="textHidden">0</h1>}
                <h1>Education</h1><br/>
                <Table id="tblEducationInfo" hover>
                    <thead>
                        <tr>
                            <th className="paddedHeading">Name</th>
                            <th className="paddedHeading">University</th>
                            <th className="paddedHeading">Course name</th>
                            
                        </tr>
                    </thead>
                    <tbody>
                    {(this.state.users).map(user => (
                        <tr key={user.ID+Math.random()*100}>
                            <td className="paddedRow">{user.name}</td>
                            <td className="paddedRow">{user.university}</td>
                            <td className="paddedRow">{user.course_name}</td>
                        </tr>
                    ))}
                    </tbody>            
                </Table>

                {<h1 className="textHidden">{this.usersCounter = -1}</h1>}
                <h1>Bank details</h1><br/>
                <Table hover>
                    <thead>
                        <tr>
                            <th></th>
                            <th>Name</th>
                            <th>Bank account number</th>                           
                        </tr>
                    </thead>
                    <tbody>
                    {(this.state.users).map(user => (
                        <tr key={user.ID+Math.random()*100}>
                            {<h1 className="textHidden">{++this.usersCounter}</h1>}
                            <td>{user.name}</td>
                            <td>{decryptedAccNums[this.usersCounter]}</td>
                        </tr>
                    ))}
                    </tbody>            
                </Table>
                
            </div>

        )
    }

    async DeleteUser(_id){
        await axios.post('/user/delete/'+_id)
        .then(async(res) => {
            console.log("Deleted:\n"+res.data);
            await this.GetUserList();
        })
    }

    async GetUserList(){
        await axios.get('/users' , {})
        .then((res) => {
            //console.log("Job Manager retrieved: ", res.data.jobs);
            //this.setState( {users: Array.from(res.data.users)} );
            this.setState( {users: res.data.result} );
        })
   }
}